import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

import api from "../api";
import EditAndLogout from "../components/EditAndLogout";

function Admin() {
  const { t } = useTranslation(undefined, { keyPrefix: "admin" });

  const user = JSON.parse(sessionStorage.getItem("user"));

  const [boards, setBoards] = useState([]);

  useEffect(() => {
    const user = JSON.parse(sessionStorage.getItem("user"));
    api
      .adminBoards(user.id)
      .then((res) => {
        setBoards(res.data);
      })
      .catch((error) => {
        console.log(error.message);
      });
  }, []);

  return (
    <div>
      <nav
        aria-label="breadcrumb"
        className="navbar navbar-light bg-white px-4"
      >
        <ol className="breadcrumb m-0">
          <li className="breadcrumb-item active" aria-current="page">
            {t("title")}
          </li>
        </ol>
        <div>
          <span className="m-3">{user.name}</span>
          <EditAndLogout />
        </div>
      </nav>
      <div
        className={"text-center " + (boards.length === 1 ? "page-center" : "")}
      >
        <div className="row justify-content-center m-5">
          <h1 className="mb-4">{t("game-boards.title")}</h1>
          {boards.map((board) => {
            return (
              <div className="col-sm-12 col-md-6 col-lg-3">
                <div className="card m-2 p-3">
                  <img
                    src={board.image}
                    className="card-img-top mx-auto rounded-circle border"
                    style={{
                      objectFit: "cover",
                      width: "250px",
                      height: "250px",
                    }}
                    alt={"board " + board.name}
                  />
                  <div className="card-body">
                    <h2 class="card-title">{board.name}</h2>
                    <p className="card-text mb-1 mt-3">{board.description}</p>
                    <Link to={`/admin/${board.name}`}>
                      <button className="btn btn-primary btn-lg mt-4">
                        {t("game-boards.open")}
                      </button>
                    </Link>
                  </div>
                </div>
              </div>
            );
          })}
          <Link to="/admin/boards/new">
            <button className="btn btn-lg btn-primary mt-4">
              {t("game-boards.create")}
            </button>
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Admin;
